select.wmpotify-aero:not([multiple]),
.main-dropDown-dropDown {
  font: var(--font);
  padding: 2px 3px;
  border: var(--button-border);
  border-color: var(--button-border-color);
  border-radius: var(--border-radius);
  box-shadow: var(--button-shadow);
  box-sizing: border-box;
  color: #222;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: relative;
  padding-right: 30px;
  background: url("resources/icon/button-down.svg"), var(--button-gradient);
  background-position: center right;
  background-repeat: no-repeat;

  &:not(:disabled):not([data-disabled]):hover {
    border-color: var(--button-border-color-hovered);
    background-image: url("resources/icon/button-down.svg"),
      var(--button-gradient-hovered);
  }

  &:not(:disabled):not([data-disabled]):active {
    outline: none;
    border-color: var(--button-border-color-active);
    box-shadow: var(--button-shadow-active);
    background-image: url("resources/icon/button-down.svg"),
      var(--button-gradient-active);
  }

  &:not(:disabled):not([data-disabled]):focus {
    color: #222;
    outline: 1px dotted #000;
    outline-offset: -4px;
    box-shadow: inset 0 0 0 2px var(--button-shade-light-active);
  }

  & .label {
    border: none !important;
    background: transparent !important;
    color: unset !important;
  }
}

.main-dropDown-dropDown {
  height: auto;
}